html {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

body {
  min-width: 320px;
  font-family: "Poppins", "Arial", sans-serif; }

button {
  cursor: pointer; }

h1, h2, h3 {
  font-family: "Cormorant Garamond", "Palatino", serif; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  bottom: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset 0%;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.page-header {
  display: flex;
  flex-direction: column;
  align-items: center; }

.page-header__top {
  width: 100%;
  border-bottom: 1px solid #f6f5f4; }
  @media (max-width: 1023px) {
    .page-header__top {
      border: none; } }

.page-header__container {
  display: flex;
  justify-content: space-between;
  padding: 30px 30px 11px;
  margin: 0 auto;
  width: 100%;
  max-width: 1240px;
  min-height: 115px; }

.page-header__logo {
  display: block;
  margin-right: 50px; }
  @media (max-width: 1023px) {
    .page-header__logo {
      margin-right: 0; } }

.page-header__user {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 7px;
  width: 129px; }
  .page-header__user a {
    margin: 5px;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #1d1613; }
    .page-header__user a:hover {
      color: #a87b62;
      transition: 0.3s; }
  @media (max-width: 1023px) {
    .page-header__user {
      width: auto;
      margin-top: 0;
      padding: 25px 15px 26px 22px; }
      .page-header__user a {
        display: none; } }

.page-header__basket {
  display: flex;
  gap: 6px; }

.page-header__basket-icon {
  display: none; }
  @media (max-width: 1023px) {
    .page-header__basket-icon {
      display: block;
      fill: none;
      stroke: #1d1613; }
      .page-header__basket-icon:hover {
        stroke: #a87b62;
        transition: 0.3s; } }

.page-header__nav ul {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 0;
  padding: 18px 0 19px;
  width: 507px;
  list-style: none; }

.page-header__nav li {
  margin-right: 20px;
  margin-left: 12px; }

.page-header__nav li:last-child {
  margin-right: 0; }

.page-header__nav a {
  position: relative;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #1d1613; }
  .page-header__nav a:hover {
    color: #a87b62;
    transition: 0.3s; }
    .page-header__nav a:hover::after {
      position: absolute;
      display: block;
      content: "";
      width: 100%;
      height: 1px;
      background-color: #a87b62; }

@media (max-width: 1023px) {
  .page-header__nav ul {
    display: none; } }

.page-header__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .page-header__toggle {
      display: block;
      position: relative;
      top: 5px;
      left: 0;
      z-index: 6;
      padding: 0;
      width: 55px;
      height: 55px;
      background-color: #ffffff;
      background-repeat: no-repeat;
      background-position: center;
      border: 0;
      cursor: pointer; } }

.page-header__toggle-line {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 25px;
  height: 2px;
  font-size: 0;
  color: transparent;
  background-color: #1d1613;
  transition: 0.3s; }
  .page-header__toggle-line::after {
    top: 8px; }
  .page-header__toggle-line::before {
    top: -8px; }

.page-header__toggle-line::after,
.page-header__toggle-line::before {
  content: "";
  position: absolute;
  left: 0;
  width: 25px;
  height: 2px;
  background-color: #1d1613;
  transform-origin: 50% 50%;
  transition: 0.3s, transform 0.3s; }

.search {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 184px;
  margin-top: 14px; }
  .search input {
    height: 14px;
    font-family: "Poppins", "Arial", sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    border: none;
    outline: 0; }
  .search input::placeholder {
    font-family: "Poppins", "Arial", sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #9B9997; }
  @media (max-width: 1023px) {
    .search {
      display: none; } }

.search__icon {
  margin-left: 5px;
  margin-right: 13px; }

.page-main {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto; }

.page-footer {
  flex-shrink: 0; }
